.wrap2 {
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    margin-left: 80px;
    top:30px;
    -webkit-transform: rotateX(11deg) rotateY(0deg);
            transform: rotateX(11deg) rotateY(0deg);
    width: 60px;
    height: 20px;
    /*perspective: 500px;*/
    -webkit-animation: rotate 5s linear infinite;
            animation: rotate 5s linear infinite;
}
@-webkit-keyframes rotate {
    from {
        -webkit-transform: rotateX(11deg) rotateY(0deg);
                transform: rotateX(11deg) rotateY(0deg);
    }
    to {
        -webkit-transform: rotateX(11deg) rotateY(360deg);
                transform: rotateX(11deg) rotateY(360deg);
    }
}
@keyframes rotate {
    from {
        -webkit-transform: rotateX(11deg) rotateY(0deg);
                transform: rotateX(11deg) rotateY(0deg);
    }
    to {
        -webkit-transform: rotateX(11deg) rotateY(360deg);
                transform: rotateX(11deg) rotateY(360deg);
    }
}
.wrap2 >div {
    position: absolute;
    width: 60px;
    height: 20px;
    background-color: #ffffff;
    opacity: .8;
    line-height: 20px;
    text-align: center;
    font-size: 12px;
    color: #000000;
}
.wrap2 >div.one {
    -webkit-transform: translateZ(-60px);
            transform: translateZ(-60px);
}
.wrap2 >div.two {
    -webkit-transform: rotateY(60deg) translateZ(-60px);
            transform: rotateY(60deg) translateZ(-60px);
}
.wrap2 >div.three {
    -webkit-transform: rotateY(120deg) translateZ(60px) rotateX(180deg) rotateZ(180deg);
            transform: rotateY(120deg) translateZ(60px) rotateX(180deg) rotateZ(180deg);
}
.wrap2 >div.four {
    -webkit-transform: rotateY(180deg) translateZ(-60px);
            transform: rotateY(180deg) translateZ(-60px);
}
.wrap2 >div.five {
    -webkit-transform: rotateY(240deg) translateZ(-60px);
            transform: rotateY(240deg) translateZ(-60px);
}
.wrap2 >div.six {
    -webkit-transform: rotateY(300deg) translateZ(60px) rotateX(180deg) rotateZ(180deg);
            transform: rotateY(300deg) translateZ(60px) rotateX(180deg) rotateZ(180deg);
}
.wrap2 >div.seven {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
.wrap2 >div.eight {

}


